import api from '@/modules/api'
import appG from '@/modules/appGlobal'

export default {
  data() {
    return {
      title: 'hello world!',
      indicatorDots: true,
      autoplay: true,
      interval: 2001,
      duration: 500,
      banners: [],
      banners0: []
    }
  },
  onLoad() {
    this.api_200()
  },
  methods: {
    api_200() {
      let that = this
      api.post(api.api_200, api.getSign(), function (ele, res) {
        if (res.data.Basis.State == api.state.state_200) {
          that.banners = res.data.Result.banners
          that.banners0 = res.data.Result.banners0
        }
      })
    },
    /**
     * banner点击事件
     */
    bannerSelect(item) {
      let url = ''
      //商品列表
      if (item.content_type == 2) {
        url = '../category/index?id=' + item.content_value
        //商品详情
      } else if (item.content_type == 5) {
        url = '../category/details?id=' + item.content_value
        //课程活动详情
      } else if (item.content_type == 10) {
        url = '../activity/details?id=' + item.content_value
      }

      uni.navigateTo({
        url: "../user/index?id=42603"
      })
      //   uni.navigateTo({
      //     url: url
      //   })

    }
  }
}
